
import { defineComponent, reactive, toRefs, ref, onMounted, watch, computed } from 'vue'
import Logo from './Logo.vue';

type ColorMode = 'auto' | 'light' | 'dark';
interface State {
  colorMode: ColorMode;
}

export default defineComponent({
  name: 'Header',
  props: {
    iconNums: {
      type: Number,
      required: true
    },
    searchText: {
      type: String,
      required: true
    },
  },
  components: {
    Logo,
  },
  setup(props, { emit }) {
    const state = reactive<State>({
      colorMode: 'auto',
    })
    const searchRef = ref<HTMLInputElement>()
    onMounted(() => {
      state.colorMode = window.matchMedia("(prefers-color-scheme: dark)").matches ? 'dark' : 'light'
    })
    watch(
      () => ({...state}),
      (next, prev) => {
        if (next.colorMode !== prev.colorMode) {
          document.documentElement.setAttribute('data-color-mode', next.colorMode)
        }
      },
    )
    const visibilityChanged = (isVisible: boolean) => {
      if (!isVisible) {
        emit('close')
      }
    }
    const changeColorMode = (color: ColorMode) => {
      state.colorMode = color
    }
    const placeholder = computed(() => `Search ${props.iconNums} icons (Press "/" to focus)`)
    const onKeyDown = (event: KeyboardEvent) => {
      if (event.code === 'Slash') {
        if (searchRef.value) {
          searchRef.value.focus()
        }
      }
    }
    document.addEventListener('keyup', onKeyDown)
    return {
      ...toRefs(state),
      placeholder,
      searchRef,
      changeColorMode,
      visibilityChanged,
    }
  },
});
